import { Component, OnInit, Input, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';
import * as Highcharts from 'highcharts';
import { WeatherLibService } from '../../weather-lib.service';

declare var require: any;
let Boost = require('highcharts/modules/boost');
let noData = require('highcharts/modules/no-data-to-display');
let More = require('highcharts/highcharts-more');

Boost(Highcharts);
noData(Highcharts);
More(Highcharts);
noData(Highcharts);

@Component({
    selector: 'app-chart',
    templateUrl: './chart.component.html',
    styleUrls: ['./chart.component.css']
})
export class ChartComponent implements OnInit {
    @Input() currentCity: string;
    public showForecast = false;
    public currentData: string[] = [];
    public currentTemp: number[] = [];
    public highcharts = Highcharts;

    public chartOptions: Highcharts.Options = {
        title: {
            text: 'Forecast'
        },
        xAxis: {
            categories: this.currentData
        },
        yAxis: {
            title: {
                text: 'Temperature Â°C'
            }
        },
        tooltip: {
            valueSuffix: ' Â°C'
        },
        series: [
            {
                name: 'Temp',
                type: 'column',
                data: this.currentTemp
            }
        ]
    };

    constructor(private weatherLibService: WeatherLibService) {
    }

    onGetForecast(): void {
        this.weatherLibService.getForecast(this.currentCity).subscribe((value) => {
            this.currentData.splice(0, this.currentData.length);
            this.currentTemp.splice(0, this.currentTemp.length);
            for (let i = 0; i < value.length; i++) {
                this.currentData.push(value[i].data);
                this.currentTemp.push(value[i].temp);
            }
            Highcharts.chart('container', this.chartOptions);
            this.showForecast = true;
        }
        );
    }

    public onCloseForecast() {
        this.showForecast = false;
    }

    ngOnInit() {
    }

    public ngOnChanges(changes: { [propName: string]: SimpleChange }) {
        if (changes['currentCity']) {
            this.showForecast = false;
        }
    }

}
