import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Weather } from './weather.model';
import { map, catchError } from 'rxjs/operators';
import { BaseWeather } from './base-weather.service';
import { Forecast } from './forecast.model';
import { Observable, throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class GetDataService extends BaseWeather {
  private currentWeather: Weather;
  private currentForecast: Forecast[] = [];
  private months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  public errorMessage: string;

  constructor(http: HttpClient) {
    super('', http);
  }

  public getDataByName(city: string): Observable<Weather> {
    return this.getDataByCityName<Weather>(city).pipe
      (
        map((res: any) => {
          this.mapWeather(res);
          return this.currentWeather;
        }
        ),
        catchError((error: HttpErrorResponse) => {
          this.errorMessage = '';
          this.errorMessage = 'City not found. Please enter city or ZIP correctly';
          return throwError(this.errorMessage);
        })
      );
  }

  public getDataByZip(zip: number): Observable<Weather> {
    return this.getDataByCityZip<Weather>(zip).pipe
      (
        map((res: any) => {
          this.mapWeather(res);
          return this.currentWeather;
        }),
        catchError((error: HttpErrorResponse) => {
          this.errorMessage = '';
          this.errorMessage = 'City not found. Please enter city or ZIP correctly';
          return throwError(this.errorMessage);
        })
      );
  }

  public getForecastByName(city: string): Observable<Forecast[]> {
    return this.getForecastByCityName<Forecast>(city).pipe
      (
        map((res: any) => {
          this.currentForecast.splice(0, this.currentForecast.length);
          for (let i = 0; i < res.list.length; i++) {
            this.currentForecast.push(new Forecast(
              Math.round(res.list[i].temp.day),
              new Date((res.list[i].dt) * 1000).getDate() + ',' +
              this.months[new Date((res.list[i].dt) * 1000).getMonth()]
            ));
          }

          return this.currentForecast;
        })
      );
  }

  private mapWeather(res: any): void {
    this.currentWeather = new Weather(
      res.name,
      res.sys.country,
      res.weather[0].description,
      res.weather[0].icon,
      res.main.temp,
      res.main.temp_max,
      res.main.temp_min,
      res.main.pressure,
      res.main.humidity
    );
  }
}
