import { Injectable, Output } from '@angular/core';
import { GetDataService } from './shared/get-data-service';
import { Weather } from './shared/weather.model';
import { Observable } from 'rxjs';
import { Forecast } from './shared/forecast.model';

@Injectable({
  providedIn: 'root'
})
export class WeatherLibService {

  constructor(private getDataService: GetDataService) { }

  public getWeatherByName(city: string): Observable<Weather> {
    return this.getDataService.getDataByName(city);
  }

  public getWeatherByZip(zip: number): Observable<Weather> {
    return this.getDataService.getDataByZip(zip);
  }

  public getForecast(city: string): Observable<Forecast[]> {
    return this.getDataService.getForecastByName(city);
  }

}
