import { IWatherService } from './weather.interface';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

export abstract class BaseWeather implements IWatherService {
    apiKey: string;
    configUrl: string;
    units: string;

    constructor(private key: string, private http: HttpClient) {

        this.apiKey = '&appid=de4ced2e2547330fcc10bc405dddc049';
        this.configUrl = `http://api.openweathermap.org/data/2.5/${this.key}`;
        this.units = '&units=metric';
    }

    getDataByCityName<T>(city: string): Observable<T> {
        return this.get<T>(this.configUrl + 'weather?q=' + city + this.apiKey + this.units);
    }
    public get<T>(url: string): Observable<T> {
        return this.http.get<T>(url);
    }

    getDataByCityZip<T>(zip: number): Observable<T> {
        return this.get<T>(this.configUrl + 'weather?zip=' + zip + this.apiKey + this.units);
    }

    //  have not finished yet
    getForecastByCityName<T>(city: string): Observable<T> {
        return this.get<T>(this.configUrl + 'forecast/daily?q=' + city + this.apiKey + this.units);
    }
}
