import { Component, OnInit} from '@angular/core';
import { NgForm } from '@angular/forms';
import { WeatherLibService } from './weather-lib.service';
import { Weather } from './shared/weather.model';
import { Forecast } from './shared/forecast.model';

@Component({
  selector: 'lib-weather-lib',
  templateUrl: './weather-lib.component.html',
  styles: []
})
export class WeatherLibComponent implements OnInit {
  public city: any;
  public zip: number;
  public weather: Weather;
  public forecast: Forecast[];
  public errorMessage: string;

  constructor(private getWeatherDataService: WeatherLibService) { }

  ngOnInit() {
  }

  public onSubmit(form: NgForm): void {
    this.errorMessage = '';
    this.city = form.value.city;
    if (isNaN(this.city)) {
      this.getWeatherDataService.getWeatherByName(this.city).subscribe((value) => {
        this.weather = value;
      },
        (error) => {
          this.errorMessage = error;
        }
      );
    } else {
      this.zip = this.city;
      this.getWeatherDataService.getWeatherByZip(this.zip).subscribe((value) => {
        this.weather = value;
      },
        (error) => {
          this.errorMessage = error;
        }
      );
    }
    form.reset();
  }
}
